body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  overflow: hidden;
}

.header {
  background-color: firebrick;
  color: #fff;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.header h1 {
  font-size: 25px;
  margin: 0;
  padding: 0;
  text-align: center;
}

.map {
  height: 100vh;
  position: relative;
  width: 100vw;
}

.location-icon {
  color: red;
  font-size: 2rem;
}

.loader {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.loader img {
  width: 400px;
}

.loader h1 {
  margin-top: -100px;
}

.location-info {
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  color: #fff;
  font-size: 18px;
  min-height: 200px;
  padding: 20px;
  position: absolute;
  right: 50px;
  top: 80px;
  width: 400px;
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
}
